.bigwrap{
    width: 1200px;
    margin: 0 auto;
    font-size: 12px;
    background-color: #fff;
}
.toppic{
    margin: 10px auto 0;
    border: 1px solid #dcdddd;
}
.topnav{
    color: #646464;
    line-height: 40px;
    height: 40px;
    padding-bottom: 8px;
    position: relative;
    z-index: 10000;
    a:nth-of-type(1){
        font-size: 14px;
    }
    a{
        color: #646464;
    }
    .gt{
        margin: 1px 6px 0;
    }
    span{
        color: #323232;
    }
}
.main{
    .pic_info{
        float: left;
        width: 320px;
        padding: 0 20px 30px 0;
        position: relative;
    }
    .show_info{
        float: left;
        width: 860px;
        color: #646464;
    }
}
.pic_info{
    .st{
        width: 320px;
        height: 320px;
        margin: 0 auto;
        position: relative;
        img{
            display: block;
            width: 320px;
            height: 320px;
        }
        .sf{
            width: 20px;
            height: 20px;
            // background-colorgb(231, 137, 153)ink;
            background: url('http://product.dangdang.com/images/zoom_pup.png');
            position: absolute;
            left: 0;
            top: 0;
            visibility: hidden;
            cursor:move;
        }
    }
    .df{
        border: 1px solid #f5f5f5;
        position: absolute;
        left: 320px;
        top: 0;
        z-index: 1111;
        width: 400px;
        height: 400px;
        overflow: hidden;
        background: #fff;
        text-align: center;
        visibility: hidden;
        img{
            position: absolute;
            width: 800px;
            height: 800px;
        }
    }
}
#ulist{
    width: 320px;
    margin: 8px auto 0;
    position: relative;
    #left,#right{
        position: absolute;
        height: 56px;
        width: 14px;
        top: 0;
        background-image:url(../img/product_sprites.png);
        background-position: 0 0;
        background-repeat: no-repeat;
        background-color: #969696;
    }
    #left:hover,#right:hover{
        background-color: #fb7a80;
    }
    #left{
        left: 0;
    }
    #right{
        background-position: -15px 0;
        right:0;
    }
    #list{
        overflow: hidden;
        position: relative;
        z-index: 2;
        left: 0px;
        width: 295px;
        height: 56px;
        margin-left: 14px;
        ul{
            position: absolute;
        }
        li{
            overflow: hidden;
            float: left;
            width: 56px;
            height: 56px;
            margin-right: 3px;
            cursor: pointer;
            img{
                display: block;
                width: 54px;
                height: 54px;
            }
        }
    }
}
.share_div{
    padding-top: 5px;
    position: relative;
    li{
        float: left;
        padding-right: 20px;
        color: #969696;
        span{
            float: left;
            width: 13px;
            height: 13px;
            overflow: hidden;
            background: url(../img/product_sprites.png) no-repeat 0 -60px;
            margin: 2px 5px 0 0;
            position: relative;
        }
    }
    li:nth-of-type(2) span{
        background-position: -15px -60px;
    }
    li:nth-of-type(3) span{
        background-position: -30px -60px;
    }
}

.show_infoleft{
    width: 630px;
    float: left;
    .name_info{
        font-weight: 400;
        border-bottom: 1px solid #f5f5f5;
        padding: 0 6px 12px 0;
        line-height: 24px;
        h1{
            line-height: 24px;
            max-height: 48px;
            overflow: hidden;
            color: #323232;
            font-size: 18px;
            margin: 0 auto;
            img{
                position: relative;
                top: 2px;
                display: inline;
            }
        }
        h2{
            padding-top: 6px;
            max-height: 96px;
            overflow: hidden;
            font-size: 16px;
            line-height: 24px;
            color: #323232;
            font-style: normal;
        }
    }
}
.message_info{
    margin-top: 5px;
    span{
        display: inline-block;
        padding-right: 25px;
        a{
            color: #1a66b3;
        }:hover{
            text-decoration: underline;
        }
    }
    .pinglun{
        height: 24px;
        line-height: 24px;
        margin-top: 5px;
    }
    .pinglun span{
        float: left;
    }
    .pinglun .span2{
        float: left;
        width: 45px;
        height: 12px;
        background: url(http://product.dangdang.com/images/icon_star.png) repeat-x 0 0;
        margin: 6px 6px 0 0;
        overflow: hidden;
    }
}
.price_info{
    width: 100%;
    height: 143px;
    background-color: #fcfaf7;
    padding: 0 0 0 10px;
    margin: 6px 0 0 0;
    color: #8f8f8f;
    .priceleft{
        float: left;
        padding: 10px 5px 0 0;
        a{
            color: #1a66b3;
        }
        .p2{
            color: #e52222;
        }
        .p2 span{
            font-size: 14px;
        }
        .p2 span:nth-of-type(2){
            font-size: 26px;
        }
        .p3{
            line-height: 35px;
        }
    }
    img{
        float: right;
    }
}
.area_info{
    padding-top: 6px;
    height: 24px;
    line-height: 24px;
    .arealeft{
        float: left;
        width: 68px;
        color: #969696;
        white-space: nowrap;
        overflow: hidden;
        letter-spacing: 6px;
    }
    .arearight{
        float: left;
        width: 562px;
        color: #646464;
        position: relative;
        z-index: 1000;
        span{
            float: left;
            border: 1px solid #ccc;
            height: 18px;
            line-height: 16px;
            padding: 0 14px 0 5px;
            background: #fff url(../img/product_sprites.png) no-repeat right -313px;
            cursor: pointer;
            margin: 3px 8px 0 0;
            position: relative;
            z-index: 88;
        }
        .area_pop{
            position: absolute;
            width: 369px;
            border: 1px solid #ccc;
            background-color: #fff;
            z-index: 77;
            padding: 0 2px;
            font-size: 12px;
            top: 21px;
            left: 0;
            .btn{
                display: block;
                width: 11px;
                height: 11px;
                background: url(../img/product_sprites.png) no-repeat -25px -140px;
                overflow: auto;
                font-size: 0;
                position: absolute;
                right: 6px;
                top: 8px;
            }
        }
    }
}
.area_pop .tab {
    height: 27px;
    border-bottom: 1px solid #ccc;
    color: #646464;
    li{
        height: 20px;
        float: left;
        margin: 7px 0 -1px 7px;
        padding: 0 6px;
        background: #f1f1f1 url(../img/product_sprites.png) no-repeat right -205px;
        padding-right: 12px;
        a{
            font-size: 14px;
            line-height: 20px;
            color: #464646;
            display: block;
            text-decoration: none;
            max-width: 57px;
            overflow: hidden;
            height: 20px;
        }
    }
    li.choose{
        height: 20px;
        padding: 0 13px 0 6px;
        border: 1px solid #ccc;
        border-bottom: 0;
        background-color: #fff;
    }
}
.area_pop .list{
    padding: 4px 0 8px 17px;
    min-height: 88px;
    width: 352px;
    float: left;
    overflow: hidden;
    li{
        float: left;
        padding-left: 3px;
        line-height: 22px;
        width: 85px;
        overflow: hidden;
        height: 22px;
        a{
            color: #464646;
        }
        a:hover{
            text-decoration: underline;
        }
    }
    
}
.buy_box{
    padding: 30px 0;
    margin-bottom: 35px;
    position: relative;
    .num{
        float: left;
        height: 34px;
        border: 1px solid #e9e9e9;
        width: 38px;
        padding-right: 23px;
        overflow: hidden;
        text-align: center;
        position: relative;
        input{
            width: 38px!important;
            height: 32px;
            line-height: 32px;
            border: 0;
            background: #fff;
            text-align: center;
        }
        a{
            position: absolute;
            right: 0;
            display: block;
            width: 23px;
            height: 17px;
            background-image: url(../img/product_sprites.png);
            background-repeat: no-repeat;
            background-color: #646464;
            margin-left: 6px;
        }
        .num_add{
            background-position: -37px 0;
            top: 0;
        }
        .num_del{
            background-color: #ccc;;
            background-position: -37px -17px;
            top: 17px;
        }
    }
    .buy_box_btn{
        width: 134px;
        height: 36px;
        font-size: 16px;
        line-height: 36px;
        overflow: hidden;
        margin: 0 0 3px 10px;
        float: left;
        padding: 0 15px;
        border-radius: 3px;
        text-decoration: none;
        color: #fff;
        background: #e52222;
        cursor: pointer;
        text-align: center;
        span{
            float: left;
            width: 20px;
            height: 19px;
            background: url(../img/product_sprites.png) no-repeat -40px -35px;
            margin: 9px 4px 0 0;
        }
    }
    .tanchuang{
        position: absolute;
        left:258px;
        width: 200px;
        height: 120px;
        z-index: 99;
        border: 1px solid #ccc;
        text-align: center;
        background: #fff;
    }
}
.tanchuang{
    padding: 15px;
    .success{
        height: 30px;
        line-height: 30px;
        text-indent: 15px;
        font-size: 14px;
        text-align: right;
        overflow: hidden;
        color: #646464;
        background: url('http://shopping.dangdang.com/shoppingcart/images/icon_r_201404.png')no-repeat 0 0;
        margin-bottom: 47px;
    }
    .org_btnb{
        display: inline-block;
        padding: 5px 10px;
        background: #fe8d00;
        border-radius: 3px;
        color: #fff;
        text-align: center;
    }
    .promo_btn{
        display: inline-block;
        margin-left: 5px;
        padding: 5px 10px;
        background: #f0f0f0;
        border-radius: 3px;
        color: #969696;
        text-align: center;
    }
}
.show_inforight{
    float: left;
    height: 500px;
    width: 180px;
    padding-left: 10px;
    margin:0 0 30px 29px;
    border-left: 1px solid #ccc;
    margin-bottom: 30px;
    
}

.louti{
    width: 82px;
    position: fixed;
    left: 50%;
    margin-left: 518px;
    top: 55px;
    right: auto;
    background-color: #fff;
    z-index: 999;
    color: #666;
}
#louti{
    li.title{
        height: 32px;
        line-height: 32px;
        border-bottom: 1px dotted #e9e9e9;
    }
    li{
        height: 36px;
        font-size: 12px;
        line-height: 36px;
        color: #646464;
        position: relative;
        padding-left: 12px;
        a{
            color: #646464;
        }
        .icon{
            position: absolute;
            left: 0;
            top: 15px;
            display: block;
            width: 8px;
            height: 8px;
            overflow: hidden;
            background: url(../img/product_sprites.png) no-repeat 0 -500px;
        }
    }
    li.hover a{
        color: #ff2832 !important;
        .icon{
            background-position:0 0;
        }
    }
    
}
.details_wrap{
    width: 1200px;
    margin: 0 auto;
    padding-top: 30px;
    display: flex;
    justify-content: center;
    border-top: 3px solid #e52222;
    .details_pic{
        width: 915px;
        margin-right: -84px;
        .title{
            border-bottom: 2px solid #e5e5e5;
            height: 22px;
            margin-bottom: 20px;
            margin-top: 20px;
            span{
                float: left;
                border-left: 2px solid #ff2832;
                padding: 0 30px 0 6px;
                background: #e5e5e5 url(../img/product_sprites.png) no-repeat right -525px;
                height: 22px;
                line-height: 22px;
                padding-bottom: 2px;
                margin-bottom: -2px;
                position: relative;
                color: #323232;
            }
        }
        img{
            display: block;
            padding-left: 14px;
            width: 750px;
        }
    }
}
.tab_panel_fixed{
    position: fixed;
    top: 0;
    background-color: #fff;
    border-top: 2px solid #ff2832;
    width: 1198px;
    height: 45px;
    z-index: 1000;
    border-left: 1px solid #bababa;
    border-right: 1px solid #bababa;
    border-bottom: 1px solid #bababa;
    .fixedleft{
        width: 210px;
        float: left;
        padding: 8px 0 0 20px;
        font-weight: bold;
        font-size: 12px;
        line-height: 24px;
        color: #646464;
        height: 24px;
        overflow: hidden;
        .icon{
            float: left;
            width: 18px;
            height: 18px;
            overflow: hidden;
            background-image: url(../img/product_sprites.png);
            background-position: 0 -230px;
            background-repeat: no-repeat;
            margin: 5px 5px 0 0;
        }
        .wenzi{
            float: left;
            font-size: 12px;
            line-height: 24px;
            color: #646464;
        }
        .kf{
            width: 26px;
            height: 26px;
            float: left;
            background: url(http://product.dangdang.com/images/icon_dj.jpg) no-repeat 0 0;
            overflow: hidden;
            line-height: 90px;
            margin: 1px 0 0 5px;
            cursor: pointer;
        }
    }
    .fixedright{
        float: right;
        height: 48px;
        .text{
            float: left;
            line-height: 48px;
        }
        span{
            font-weight: bold;
            font-size: 16px;
            color: #ff2832;
        }
        .btn{
            float: right;
            width: 110px;
            height: 36px;
            line-height: 36px;
            font-size: 16px;
            overflow: hidden;
            margin: 4px 5px 3px 10px;
            text-align: center;
            border-radius: 3px;
            text-decoration: none;
            color: #fff;
            background-color: #ff2832;
            cursor: pointer;
        }
    }
}
.descrip{
    color: #656565;
    line-height: 22px;
    padding: 14px 20px;
    font-size: 14px;
}
.price_indication {
    padding: 11px 20px 15px;
    background: #fafafa;
    line-height: 22px;
    color: #3c3c3c;
}
.right3_li li {
    height: 150px;
    width: 220px;
    margin-left: 9px;
    position: relative;
    border-bottom: 1px dotted #d8d8d8;
    vertical-align: top;
    display: flex;
    align-items: center;
    a{
        font-size: 12px;
        color: #646464;
    }
}